n\r\n  &__header {\r\n    text-transform: uppercase;\r\n    color: var(--neutral-White);\r\n    text-align: center;\r\n    font-size: clamp(1rem, 1.5rem + 1vw, 3rem);\r\n    letter-spacing: 0.4ch;\r\n    padding-top: 3rem;\r\n    @include m.header-font(900);\r\n  }\r\n\r\n  &__arrow-down {\r\n    display: block;\r\n    margin: 5rem auto 0;\r\n  }\r\n}\r\n","// typography\r\n@mixin header-font($weight) {\r\n  font-family: \"Fraunces\", sans-serif;\r\n  font-weight: $weight;\r\n}\r\n\r\n//media queries\r\n\r\n@mixin breakpoint($point) {\r\n  @if $point==md-up {\r\n    @media screen and (min-width: 50em) {\r\n      @content;\r\n    }\r\n  } @else if $point==sm-up {\r\n    @media screen and (min-width: 30em) {\r\n      @content;\r\n    }\r\n  }\r\n}\r\n\r\n//flex\r\n\r\n@mixin flex-center($direction) {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  flex-direction: $direction;\r\n}\r\n\r\n//first section written container\r\n\r\n@mixin format-first-section-item($color, $hoverColor) {\r\n  max-width: 80%;\r\n  text-align: center;\r\n  margin-inline: auto;\r\n  margin-block: 3rem;\r\n  @include flex-center(column);\r\n  gap: 2rem;\r\n\r\n  & > *:first-child {\r\n    font-size: clamp(2rem, 1.8rem + 1vw, 2.2rem);\r\n    @include header-font(900);\r\n    color: var(--neutral-v-DarkDeSatBlue);\r\n  }\r\n\r\n  & > *:nth-child(2) {\r\n    color: var(--neutral-DarkGrayishBlue);\r\n  }\r\n\r\n  & > *:last-child {\r\n    @include header-font(900);\r\n    margin-left: 0.5rem;\r\n    font-size: 1rem;\r\n    text-transform: uppercase;\r\n    border: none;\r\n    background-color: transparent;\r\n    position: relative;\r\n    cursor: pointer;\r\n\r\n    &::after {\r\n      content: \"\";\r\n      position: absolute;\r\n      inset: auto auto 0 0;\r\n      z-index: -1;\r\n      width: 120%;\r\n      margin-left: -10%;\r\n      height: 0.5rem;\r\n      background-color: $color;\r\n      border-radius: 5rem;\r\n    }\r\n\r\n    &:hover::after {\r\n      